@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    background: #FFFFFF;
    border-radius: 8px;
    color: $primary;
    svg{
        fill:$primary;
        stroke:$primary;
    }
}


.m-btn-primary-outline,
.m-btn-primary{
    font-weight: 500;
    font-size: 16px;
    &:hover,
    &:active,
    &:disabled{
        color: #fff;
        svg{
            fill:#fff;
            stroke:#fff;
        }
        path{
            fill: #fff;
            &.stroke-path{
                fill:none;
                stroke:#fff;
            }
        }
    }
    &:hover{
        background-color: $primaryHover;
        border-color: $primaryHover;
    }
    &:active{
        background-color: $darkBlue;
        border-color: $darkBlue;
    }
    &:disabled{
        background: $outlineColor;
        border-color: $outlineColor;
    }
}

.m-btn-primary2{
    background-color: $primary;
    color: #fff;
    svg{
        fill:#fff;
        stroke:#fff;
    }
}

.m-btn-primary-outline{
    border: 1px solid $outlineColor;
    border-radius: 8px;
    color: $primary;
    svg{
        fill:$primary;
        stroke:$primary;
    }
    &:active{
        border-color: $darkBlue;
        background-color: transparent;
        color: $darkBlue;
        svg{
            fill:$darkBlue;
            stroke:$darkBlue;
        }
        path{
            fill: $darkBlue;
            &.stroke-path{
                fill:none;
                stroke:$darkBlue;
            }
        }
    }
}

.arrow-link{
    font-weight: 500;
    font-size: 16px;
    line-height: 135%;
    display: inline-flex;
    align-items: center;
    color: $primary;
    svg{
        stroke: $primary;
        margin-left: 8px;
    }
    &:hover{
        color: $primaryHover;
        svg{
            stroke:$primaryHover;
        }
    }
}
