.catalog-item{
  padding: 0 0 50px;
  &__wrap{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 10px));
    gap: 20px;
  }
  .breadcrumbs{
    position: relative;
    padding-right: 55px;
  }
  &__buttons{
    display: flex;
    align-items: center;
    top: 0;
    right: 0;
    position: absolute;
  }
  &__btn{
    margin-left: 12px;
    &:hover{
      path{
        stroke: $primary;
      }
      circle{
        fill: $primary;
      }
    }
  }
}

.gallery{
  padding-left: 122px;
  position: relative;
  &__loop{
    position: absolute;
    top: 16px;
    right: 16px;
    z-index: 2;
  }
  .swiper{
    &-slide{
      img{
        width: 100%;
      }
    }
    &-button{
      left: 0;
      right: unset;
      background: #FFFFFF;
      box-shadow: 0px 5px 5px -2px rgba(0, 130, 201, 0.2);
      border-radius: 4px;
      width: 100%;
      height: 34px;
      &-prev{
        margin: 0;
        top: 0;
      }
      &-next{
        top: unset;
        bottom: 0;
      }
    }
  }
  &__small{
    padding: 48px 0;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 90px;
    .swiper{
      height: 100%;
      &-slide{
        height: 90px;
        background-color: #fff;
        display: flex;
        align-items: center;
        position: relative;
        cursor: pointer;

        img{
          max-height: 100%;
        }
        &:before{
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          height: 2px;
          width: 100%;
          background: $primary;
          opacity: 0;
          transition: all .3s;
        }
        &-thumb-active{
          &:before{
            opacity: 1;
          }
        }
      }
    }
  }
  &__big{
    .swiper{
      background: #FFFFFF;
      &-slide{
        a{
          display: block;
          position: relative;
          padding-bottom: 76.6%;
          img{
            position: absolute;
            width: 100%;
            max-width: 78.3%;
            max-height: 90%;
            object-fit: cover;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
          }
        }
      }
    }
  }
}

.product-detail{
  padding: 50px 0;
  &__inner{
    display: grid;
    grid-template-columns: auto 467px;
    gap: 20px;
  }
  &__tabs{
    .tab{
      height: 46px;
      padding: 0 28px;
      font-weight: 500;
      font-size: 16px;
    }
  }
  &__content{
    margin-top: 40px;
    .content-text{
      margin-top: 0;
    }
  }
}

.variants{

  table{
    width: 100%;
  }
  th,
  td{
    padding: 12px 28px;
    font-size: 16px;
    line-height: 135%;
    background-color: #fff;
    &:first-child{
      width: 127px;
    }
    &:last-child{
      width: 286px;
    }
  }
  th{
    height: 62px;
    font-weight: 500;
  }
  td{
    height: 46px;
  }
  tbody tr{
    &:nth-child(odd){
      td{
        background-color: transparent;
      }
    }
  }
}

.docs{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 20px;
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 62px;
      margin-right: 12px;
      flex-shrink: 0;
    }
  }
}

.help{
  padding: 24px 31.5px 0;
  border-radius: 12px;
  border: 1px solid $outlineColor;
  display: grid;
  grid-template-columns: auto 180px;
  align-items: flex-end;
  gap: 4px;
  &__content{
    padding-bottom: 32px;
  }
  &__desc{
    margin: 4px 0 16px;
  }
  &__btn{
    font-size: 14px;
    margin-top: 12px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
}

.history{
  padding: 50px 0;
  &__slider{
    margin-top: 40px;
    position: relative;
    .swiper{
      &-container{
        overflow: hidden;
      }
    }
  }
}

.info--product{
  .info__slider{
    margin-top: 0;
  }
}
