@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper-pagination{
  position: static;
  margin-top: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  &-bullet{
    width: 80px;
    height: 2px;
    border-radius: 3px;
    opacity: .5;
    background-color: $primary;
    margin: 0 8px !important;
    position: relative;
    &-active{
      opacity: 1;
    }
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      height: 1200%;
      width: 100%;
    }
  }
}

.swiper-button{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #FFFFFF;
  box-shadow: $shadow;
  svg{
    stroke: $primary;
  }
  &-prev{
    left: -25px;
  }
  &-next{
    right: -25px;
  }
}

.tabs{
  display: flex;
  align-items: center;
}

.tab{
  padding: 0 14px;
  height: 37px;
  display: flex;
  align-items: center;
  border-radius: 6px;
  border: 1px solid $outlineColor;
  cursor: pointer;
  font-size: 14px;
  &:not(:last-child){
    margin-right: 4px;
  }
  &:hover,
  &._active{
    color: $primary;
    border-color: $primary;
  }
  &._active{
    pointer-events: none;
  }
  &-content{
    display: none;
    &._active{
      display: block;
    }
  }
}

.m-pagination{
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__more{
    height: 46px;
    padding: 0 26px;
  }
  &__wrap{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
  }
  &__item{
    margin: 0 4px;
    width: 33px;
    height: 33px;
    justify-content: center;
    border-radius: 50%;
    &._active{
      background-color: $primary;
      color: #fff;
      pointer-events: none;
    }
  }
  &__item,
  &__btn{
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 120%;
  }
  &__btn{

    &:first-child{
      margin-right: 36px;
    }
    &:last-child{
      margin-left: 36px;
    }
    svg{
      &:first-child{
        margin-right: 4px;
      }
      &:last-child{
        margin-left: 4px;
      }
    }
  }
}


@media screen and (max-width: 767px){
  .swiper-pagination{
    margin-top: 16px;
    &-bullet{
      margin: 0 4px !important;
    }
  }

  .m-pagination{
    margin-top: 40px;
    &__wrap{
      margin-top: 28px;
    }
    &__item,
    &__btn{
      font-size: 12px;
    }
    &__item{
      width: 28px;
      height: 28px;
    }
    &__btn{
      &:first-child{
        margin-right: 30px;
      }
      &:last-child{
        margin-left: 30px;
      }
    }
  }
}
