.blog{
  padding: 50px 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 80px 20px;
  }
}


.content-text{
  margin-top: 24px;
  ul,
  p,
  h2,
  h3{
    padding-right: 40px;
    &:not(:last-child){
      margin-bottom: 24px;
    }
  }
  ul{
    line-height: inherit;
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    padding-left: 10px;
    li{
      line-height: inherit;
      display: flex;
      &:before{
        content: '';
        flex-shrink: 0;
        display: block;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background-color: $black;
        margin-right: 11px;
        transform: translateY(9px);
      }
    }
  }
  img{
    display: inline-block;
    width: calc(50% - 40px);
    margin: 60px 35px 60px 0;
  }
}

.article{
  padding-right: 40px;
  width: 980px;
  max-width: 100%;
  &__group{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 40px;
    }
    p{
      &:not(:last-child){
        margin-bottom: 10px;
      }
    }
  }
  &__back{
    margin-top: 80px;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 135%;
    svg{
      margin-right: 8px;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
    &:active{
      path{
        stroke: $primaryHover;
      }
    }
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 60px;
    .blog-card{
      &__title{
        margin-bottom: 0;
      }
      &__desc{
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1350px){
  .blog{
    &__grid{
      grid-template-columns: repeat(3,1fr);
    }
  }
}


@media screen and (max-width: 1100px){
  .article{
    padding-right: 0;
    &__items{
      grid-template-columns: repeat(3,1fr);
      gap: 20px;
      margin-top: 100px;
    }
  }
}


@media screen and (max-width: 992px){
  .blog{
    &__grid{
      gap: 40px 20px;
    }
  }

  .article{
    &__items{
      grid-template-columns: repeat(2,1fr);
    }
  }
}


@media screen and (max-width: 767px){
  .blog{
    padding-top: 0;
    &__grid{
      grid-template-columns: repeat(2,1fr);
    }
  }

  .article{
    &__items{
      margin-top: 90px;
    }
    &__back{
      margin-top: 60px;
    }
  }

  .content-text{
    img{
      width: calc(50% - 20px);
      margin: 60px 10px 60px 0;
    }
  }
}


@media screen and (max-width: 576px){
  .blog{
    &__grid{
      grid-template-columns: repeat(1,1fr);
    }
  }

  .article{
    &__items{
      grid-template-columns: repeat(1,1fr);
    }
  }
}
