.catalog-card{
  display: block;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  padding-bottom: 78.5%;
  color: #fff;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $primary;
    z-index: 2;
    opacity: 0;
    transition: all .3s;
  }
  &:hover{
    color: #fff;
    &:before{
      opacity: 1;
    }
  }
  &__img{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    top: 0;
    left: 0;
  }
  &__title{
    padding: 24px;
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    z-index: 3;
    svg{
      stroke:#fff;
    }
  }
}

.sale-card{
  padding: 40px;
  border-radius: 16px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  &__badge{
    position: absolute;
    top: 12px;
    left: 12px;
    padding: 0 8px;
    border: 1px solid #AFC4CF;
    border-radius: 4px;
    background-color: #fff;
    font-size: 10px;
    line-height: 110%;
    letter-spacing: -0.02em;
    text-transform: uppercase;
    height: 23px;
    display: flex;
    align-items: center;
  }
  &__img{
    position: relative;
    padding-bottom: 58%;
    img{
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
      height: 100%;
      width: 100%;
      display: block;
    }
  }
  &__title{
    margin: 20px 0;
    a{
      text-decoration: underline;
    }
  }
  &__link{
    margin-top: auto;
    width: 141px;
    height: 46px;
  }
}

.sale-card2{
  padding: 32px 24px;
  background: #FFFFFF;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    position: relative;
    padding-bottom: 91%;
    overflow: hidden;
    img{
      border-radius: 8px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
      transition: all .3s;
      &:hover{
        transform: scale(1.1);
      }
    }
  }
  &__badges{
    position: absolute;
    top: 12px;
    left: 12px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__badge{
    margin: 0 4px 4px 0;
    border-radius: 4px;
    height: 23px;
    display: flex;
    align-items: center;
    padding: 0 8px;
    font-size: 10px;
    letter-spacing: -0.02em;
    text-transform: uppercase;
    &--green{
      background-color: $green;
      color: #fff;
    }
    &--yellow{
      background-color: $yellow;
      color: $black;
    }
    &--red{
      background-color: $red;
      color: #fff;
    }
  }
  &__co{
    display: block;
    margin-top: 20px;
    color: $dark;
  }
  &__title{
    display: block;
    margin-top: 12px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:4;
    -webkit-box-orient: vertical;
  }
  &__nav{
    margin-top: auto;
    padding-top: 20px;
  }
  &__btn,
  &__btn2{
    width: 100%;
    height: 46px;
  }
  &__btn{
    margin-bottom: 8px;
  }
  &__btn2{
    svg{
      fill:none !important;
      stroke:none !important;
      margin-left: 16px;
    }
    &._active{
      display: none;
      &~.sale-card2__cart{
        display: grid;
      }
    }
  }
  &__cart{
    display: none;
    grid-template-columns: 1fr 1fr;
    gap: 4px;
  }
}

.info-card{
  border: 1px solid $outlineColor;
  border-radius: 16px;
  padding: 40px;
  &__title{
    margin-top: 48px;
  }
  &__desc{
    margin-top: 12px;
  }
}

.partner-card{
  display: grid;
  grid-template-columns: auto 203px;
  gap: 24px 20px;
  padding: 40px;
  background: #FFFFFF;
  border-radius: 16px;
  &__content{
    max-width: 672px;
  }
  &__text{
    margin: 12px 0 28px;
  }
  &__img{
    img{
      max-width: 100%;
      display: block;
      margin-left: auto;
    }
  }
}

.blog-card{
  display: flex;
  flex-direction: column;
  &__img{
    margin-bottom: 28px;
    display: block;
    padding-bottom: 75.36%;
    position: relative;
    border-radius: 12px;
    overflow: hidden;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title{
    margin: 16px 0;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__desc{
    margin-top: auto;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:4;
    -webkit-box-orient: vertical;
  }
}

.cart-card{
  display: grid;
  grid-template-columns: 60px 160px calc(100% - 600px) 260px;
  align-items: center;
  gap: 40px;
  padding: 28px 40px;
  &:not(:last-child){
    border-bottom: 3px solid $lightBlue;
  }
  &__img{
    display: block;
    img{
      width: 100%;
    }
  }
  &__art{
    padding: 0 10px;
    display: inline-flex;
    align-items: center;
    white-space: nowrap;
    height: 29px;
    border-radius: 4px;
    border: 1px solid $outlineColor;
  }
  &__title{
    display: block;
    margin-top: 16px;
  }
  &__desc{
    margin-top: 4px;
  }
  &__action{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .cartcalc{
      width: 154px;
      flex-shrink: 0;
      border: none;
      padding: 0;
      input{
        color: $black;
      }
      button{
        svg{
          stroke: $black;
        }
        &:hover{
          svg{
            stroke: $primary;
          }
        }
      }
    }
  }
  &__delete{
    &:hover{
      path{
        stroke: $red;
      }
    }
  }
}

.history-card{
  display: grid;
  grid-template-columns: 70px calc(100% - 82px);
  gap: 12px;
  &__img{
    height: 70px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 8px;
    }
  }
  &__title{
    font-size: 16px;
    line-height: 135%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
  }
}

.sale-card3{
  padding: 20px 28px;
  background: #FFFFFF;
  border-radius: 12px;
  display: flex;
  align-items: center;
  &__img{
    flex-shrink: 0;
    width: 160px;
    height: 90px;
    margin-right: 35px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info{
    padding-right: 10px;
  }
  &__title{
    margin-top: 12px;
  }
  &__btn{
    flex-shrink: 0;
    margin-left: auto;
    padding: 0 20px;
    height: 46px;
    svg{
      margin-left: 16px;
    }
  }
}


@media screen and (max-width: 1100px){
  .cart-card{
    grid-template-columns: 60px 160px auto;
    gap: 20px 12px;
    &__action{
      grid-column: 3/4;
      max-width: 262px;
    }
  }
}


@media screen and (max-width: 992px){
  .blog-card{
    &__img{
      padding-bottom: 107%;
    }
  }
}


@media screen and (max-width: 767px){
  .catalog-card{
    height: 100px;
    padding-bottom: 0;
    &__title{
      padding: 16px;
    }
  }

  .sale-card{
    padding: 40px 28px;
    &__badge{
      top: 0;
      left: 0;
    }
    &__link{
      width: 130px;
      height: 41px;
      font-size: 14px;
    }
  }

  .info-card{
    padding: 32px 28px 36px;
    &__icon{
      height: 50px;
      width: auto;
    }
    &__title{
      margin-top: 28px;
    }
  }

  .partner-card{
    padding: 40px 28px;
    display: flex;
    flex-direction: column;
    gap: unset;
    &__img{
      margin-bottom: 24px;
      order: -1;
      height: 70px;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        max-height: 100%;
        margin: 0;
      }
    }
    &__content{
      display: flex;
      flex-direction: column;
    }
    &__text{
      font-size: 14px;
      line-height: 120%;
    }
    &__link{
      margin-top: auto;
    }
  }

  .blog-card{
    &__img{
      padding-bottom: 81.4%;
    }
  }

  .cart-card{
    padding: 20px 12px;
    grid-template-columns: 60px 120px auto;
    gap: 16px 8px;
    &__info,
    &__action{
      grid-column: 1/-1;
    }
    &__action{
      max-width: 100%;
    }
  }

  .sale-card3{
    flex-direction: column;
    padding: 12px;
    &__img{
      width: 100%;
      height: auto;
      padding-bottom: 56%;
      position: relative;
      margin: 0 0 16px;
      img{
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &__info{
      margin-bottom: 16px;
    }
    &__art{
      font-size: 12px;
    }
    &__title{
      margin-top: 8px;
      font-size: 14px;
    }
    &__btn{
      width: 100%;
      margin: auto 0 0;
    }
  }

  .sale-card2{
    &__img{
      padding-bottom: 73.5%;
    }
    &__co{
      font-size: 12px;
    }
    &__title{
      font-size: 14px;
      -webkit-line-clamp: 6;
    }
    &__btn,
    &__btn2
    .cartcalc{
      font-size: 14px;
      height: 41px;
      input{
        font-size: inherit;
      }
    }
  }
}
