@import "../mixins";

.header-search{
  //flex-grow: 1;
  margin-left: 20px;
  position: relative;
  width: calc(100% - 166px);
  &__field{
    input{
      padding-right: 100px;
      height: 58px;
    }
  }
  &__status{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 68px;
    width: 20px;
    height: 20px;
    opacity: 0;
    pointer-events: none;
    z-index: 2;
  }
  &__spinner{
    .spinner-border{
      width: 20px;
      height: 20px;
      border-width: 2px;
      border-color: $dark;
      border-right-color: transparent;
    }
  }
  &__clear{
    display: none;
  }
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 46px;
    height: 46px;
    right: 6px;
  }
  &._filled{
    .header-search__status{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: #000000;
    opacity: 0.3;
    display: none;
    z-index: 10;
  }
  &__results{
    position: absolute;
    top: -12px;
    left: -12px;
    width: calc(100% + 24px);
    border-radius: 12px;
    background-color: $lightBlue;
    padding: 90px 0 12px;
    z-index: 11;
    display: none;
  }
  &__scroll{
    padding-right: 4px;
  }
  &__items{
    overflow-y: auto;
    max-height: 476px;
    @include scroll;
  }
  &__all{
    padding: 16px;
    justify-content: space-between;
    width: 100%;
  }
  &__equips{
    margin-top: 32px;
  }
  &__empty{
    padding: 0 12px;
  }
  &._close-load{
    .header-search{
      &__spinner{
        display: none;
      }
      &__clear{
        display: flex;
      }
    }
  }
  &._show-results{
    .header-search{
      &__field{
        position: relative;
        z-index: 12;
      }
      &__bg,
      &__results{
        display: block;
      }
    }
  }
}

.search-card{
  padding: 8px 12px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #AFC4CF;
  &:hover{
    background-color: $lightBlue2;
    color: $primaryHover;
    .search-card__title{
      span{
        color: $primaryHover;
      }
    }
  }
  &__img{
    width: 80px;
    height: 50px;
    flex-shrink: 0;
    background-color: #fff;
    border-radius: 4px;
    overflow: hidden;
    margin-right: 12px;
    img{
      max-width: 100%;
      max-height: 100%;
      background-color: #fff;
    }
  }
  &__title{
    span{
      color: $primary;
    }
  }
  &__desc{
    margin-top: 4px;
  }
}

.search-card2{
  border: none;
}


@media screen and (max-width: 1200px){
  .header-search{
    order: -1;
    max-width: unset;
    width: 100%;
    margin: 0;
    &__results{
      width: calc(100% + 72px);
      left: -36px;
      height: calc(100vh - 210px);
      top: 100%;
      padding: 20px 36px 36px;
      overflow-y: auto;
    }
    &__items{
      overflow: unset;
      max-height: unset;
    }
    &__all{
      padding-left: 0;
      padding-right: 0;
    }
    &._show-results{
      .header-search{
        &__bg{
          display: none;
        }
      }
    }
  }

  .search-card{
    padding-left: 0;
    padding-right: 0;
  }
}


@media screen and (max-width: 840px){
  .header-search{
    &__field{
      input{
        height: 42px;
        padding: 0 40px;
        background-image: url(../img/search3.svg);
        background-repeat: no-repeat;
        background-position: left 12px center;
      }
    }
    &__status{
      right: 12px;
    }
    &__spinner{
      .spinner-border{
        width: 18px;
        height: 18px;
      }
    }
    &__clear{
      img{
        width: 18px;
      }
    }
    &__btn{
      display: none;
    }
    &__results{
      height: calc(100vh - 138px);
      width: calc(100% + 30px);
      left: -15px;
      padding: 20px 15px 15px;
    }
  }

  .search-card{
    padding: 10px 0;
    &__img{
      width: 64px;
      height: 40px;
      margin-right: 8px;
    }
    &__title{
      font-size: 14px;
      line-height: 120%;
    }
  }
}
