.footer{
  margin-top: 50px;
  background-color: #fff;
  &__logo{
    display: block;
    width: 220px;
    margin-right: 10%;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__inner{
    padding: 80px 0;
    border-top: 1px solid $outlineColor;
    display: flex;
    justify-content: space-between;
  }
  &__links{
    display: flex;
    align-items: center;
    flex-direction: column;
    flex-grow: 1;
    max-width: 345px;
    margin-right: 20px;
    font-weight: 500;
  }
  &__link{
    margin: 0 auto 12px 0;
    line-height: inherit;
  }
  &__contacts{
    &-group{
      margin-bottom: 32px;
    }
    &-desc{
      margin-top: 4px;
      color: $dark;
    }
  }
  &__btn{
    width: 100%;
    height: 46px;
  }
}

.subscribe{
  padding: 80px 0;
  display: grid;
  grid-template-columns: 460px auto;
  align-items: center;
  gap: 20px;
  width: 1196px;
  max-width: 100%;
  margin: 0 auto;
  &__desc{
    margin-top: 12px;
  }
  &__form{
    display: grid;
    grid-template-columns: auto 211px;
    gap: 12px;
  }
  &__btn{
    height: 62px;
    svg{
      margin-left: 20px;
    }
  }
}

.footer-bottom{
  background-color: $lightBlue;
  padding: 20px 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $dark;
  }
}


@media screen and (max-width: 1200px){
  .subscribe{
    grid-template-columns: 100%;
    padding-left: 30px;
    padding-right: 30px;
    text-align: center;
    &__desc{
      max-width: 420px;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .footer{
    &__inner{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 60px;
    }
    &__logo{
      grid-column: 1/-1;
      width: 242px;
      margin: 0 auto;
    }
    &__contacts{
      text-align: center;
      grid-column: 1/-1;
      max-width: 506px;
      margin: 0 auto;
    }
  }

  .footer-bottom{
    &__inner{
      flex-direction: column;
    }
    &__item{
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
  }
}


@media screen and (max-width: 767px){
  .subscribe{
    padding: 60px 0;
    &__form{
      grid-template-columns: 100%;
    }
    &__btn{
      font-size: 14px;
      height: 50px;
    }
  }

  .footer{
    &__inner{
      padding: 40px 0 60px;
      gap: 40px 6px;
    }
    &__logo{
      width: 200px;
    }
    &__link{
      margin-bottom: 12px;
    }
    &__contacts{
      width: 100%;
      &-group{
        margin-bottom: 24px;
      }
    }
    &__btn{
      height: 41px;
      font-size: 14px;
    }
  }

  .footer-bottom{
    &__item{
      &:not(:last-child){
        margin-bottom: 12px;
      }
    }
  }
}
