.about{
  padding: 50px 0;
  overflow: hidden;
  &__text{
    margin-top: 40px;
    max-width: 900px;
  }
  &__slider{
    margin-top: 60px;
    .swiper{
      &-container{
        position: relative;
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        border-radius: 20px;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &-button{
        &-disabled{
          opacity: 0 !important;
        }
      }
    }
  }
  .info__slider{
    margin-top: 100px;
  }
}


@media screen and (max-width: 992px){
  .about{
    &__slider{
      .swiper{
        &-slide{
          position: relative;
          padding-bottom: 55%;
          border-radius: 20px;
          overflow: hidden;
          img{
            position: absolute;
            top: 0;
            left: 0;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 767px){
  .about{
    padding-top: 0;
    &__slider{
      margin-top: 40px;
      .swiper{
        &-button{
          display: none;
        }
      }
    }
    .info__slider{
      margin-top: 90px;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__slider{
      .swiper{
        &-slide{
          padding-bottom: 94%;
        }
      }
    }
  }
}
