.catalog-page{
  padding-bottom: 50px;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 365px) 345px;
    justify-content: space-between;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 20px;
    margin-top: 40px;
  }
}

.types{
  margin-top: 50px;
  padding: 50px 0;
  &__group{
    margin-top: 40px;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .catalog__link{
    padding: 0 20px;
    height: 46px;
    font-size: 16px;
    &-icon-desktop{
      width: 14px;
      height: auto;
      margin-left: 16px;
    }
  }
  &__items{
    margin-top: 28px;
    column-count: 3;
    column-gap: 20px;
    column-fill: revert;
    @media screen and (min-width: 1300px){
      &._flex-columns{
        display: flex;
        flex-wrap: wrap;
        column-count: unset;
        column-gap: unset;
        .types__item{
          width: calc(100% / 3 - (40px / 3));
          align-self: flex-start;
          &:nth-child(1),
          &:nth-child(2){
            margin-right: 20px;

          }
        }
      }
    }
  }
  &__item{
    display: inline-flex;
    width: 100%;
    align-items: center;
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 12px;
    }
    &:before{
      content: '';
      width: 6px;
      height: 6px;
      border-radius: 50%;
      flex-shrink: 0;
      background-color: $primary;
      margin-right: 8px;
    }
  }
}

.catalog-nav{
  background: #FFFFFF;
  border-radius: 16px;
  position: sticky;
  top: 20px;
  &__group{
    padding: 28px 20px;
    &:not(:last-child){
      border-bottom: 4px solid $lightBlue;
    }
  }
  &__items{
    margin-top: 16px;
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
  }
  &__item{
    &-show{
      display: flex;
      align-items: center;
      justify-content: space-between;
      transition: all .3s;
      svg{
        stroke: $black;
        transition: stroke .3s, transform .4s;
      }
      &:hover{
        color: $primaryHover;
        cursor: pointer;
        svg{
          stroke:$primaryHover;
        }
      }
      &._open{
        font-weight: 500;
        svg{
          transform: rotate(180deg);
        }
      }
    }
    &-hidden{
      margin-top: 10px;
      display: none;
    }
    &-link{
      &:not(:last-child){
        margin-bottom: 6px;
      }
    }
  }
}

.partners{
  padding: 50px 0 0;
  overflow: hidden;
  &__slider{
    margin-top: 40px;
    .swiper{
      @media screen and (min-width: 768px){
        &-wrapper{
          transform: unset !important;
          display: grid;
          grid-template-columns: 100%;
          gap: 20px;
        }
        &-slide{
          width: 100% !important;
        }
        &-pagination{
          display: none;
        }
      }
    }
  }
}

.seo{
  padding: 50px 0;
  &--direction{
    .seo__group{
      max-width: 900px;
    }
  }
  &--subcategory{
    .seo{
      &__group{
        max-width: 947px;
        &:not(:last-child){
          margin-bottom: 40px;
        }
      }
      &__desc{
        p{
          &:not(:last-child){
            margin-bottom: 10px;
          }
        }
      }
    }
  }
  &._collapsable:not(._expanded){
    .seo{
      &__group{
        &:nth-child(3){
          .seo__desc{
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp:3;
            -webkit-box-orient: vertical;
            p{
              display: inline;
            }
          }
          &~.seo__group{
            display: none;
          }
        }
      }
    }
  }
  &._expanded{
    .seo{
      &__toggle{
        margin-top: 20px;
        svg{
          transform: rotate(180deg);
        }
        span{
          display: none;
        }
        &:before{
          content: '';
        }
      }
    }
  }
  &__group{
    max-width: 1075px;
    &:not(:last-child){
      margin-bottom: 60px;
    }
  }
  &__title{
    margin-bottom: 24px;
  }
  &__desc{
    margin-top: 24px;

    p{
      &:not(:last-child){
        margin-bottom: 40px;
      }
    }
  }
  &__toggle{
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 135%;
    svg{
      margin-left: 8px;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .catalog-page{
    &__grid{
      @media screen and (min-width: 1101px){
        grid-template-columns: repeat(2,1fr);
      }
    }
  }
}


@media screen and (max-width: 1300px){
  .types{
    &__items{
      column-count: 2;
    }
  }
}


@media screen and (max-width: 1100px){
  .catalog-page{
    &__inner{
      grid-template-columns: 100%;
    }
    &__sidebar{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-page{
    &__grid{
      grid-template-columns: repeat(2,1fr);
    }
  }

  .types{
    &__group{
      position: relative;
      padding-bottom: 106px;
    }
  }
}


@media screen and (max-width: 767px){
  .catalog-page{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .types{
    margin-top: 40px;
    &__group{
      padding-bottom: 58px;
      margin-top: 32px;
      &:not(:last-child){
        margin-bottom: 60px;
      }
    }
    .catalog__link{
      justify-content: unset;
      padding: 0;
      font-size: 14px;
    }
    &__items{
      column-count: 1;
      margin-top: 20px;
    }
  }

  .partners{
    &__slider{
      margin-top: 32px;
    }
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
      }
    }
  }

  .seo{
    padding-top: 40px;
    &__group{
      &:not(:last-child){
        margin-bottom: 40px;
      }
    }
  }
}
