.cart{
  padding: 50px 0;
  &__nav{
    display: flex;
    align-items: center;
  }
  &__unselect{
    margin-left: 40px;
    display: flex;
    align-items: center;
    svg{
      margin-right: 12px;
    }
    &:hover{
      color: $red;
      path{
        stroke:$red;
      }
    }
  }
  &__items{
    margin-top: 60px;
    background: #FFFFFF;
    border-radius: 20px;
  }
}

.order{
  padding: 48px;
  background-color: #FFFFFF;
  background-image: url(../img/order-bg.svg);
  background-position: right top;
  background-repeat: no-repeat;
  border-radius: 20px;
  margin-top: 20px;
  &__wrap{
    max-width: 784px;
  }
  &__desc{
    margin-top: 16px;
  }
  &__tabs{
    margin-top: 40px;
  }
  &__content{
    margin-top: 28px;
  }
  &__form{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 8px;
  }
  &__full{
    grid-column: 1/-1;
  }
  &__check{
    margin-top: 12px;
  }
  &__btn{
    margin-top: 12px;
    height: 63px;
    font-weight: 500;
    font-size: 18px;
  }
}

.cart-empty{
  padding: 150px 0;
  text-align: center;
  &__wrap{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
  }
  &__img{
    width: 200px;
    max-width: 100%;
  }
  &__title{
    margin-top: 80px;
  }
  &__desc{
    margin-top: 16px;
  }
  &__link{
    width: 259px;
    height: 59px;
    margin-top: 40px;
    svg{
      margin-left: 20px;
    }
  }
}


@media screen and (max-width: 1200px){
  .order{
    background-image: none;
    &__wrap{
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 767px){
  .cart{
    padding-top: 0;
    &__nav{
      justify-content: space-between;
    }
    &__check-all{
      label{
        &:before{
          margin-right: 8px;
        }
      }
    }
    &__unselect{
      font-size: 14px;
      svg{
        margin-right: 6px;
      }
    }
    &__items{
      margin-top: 34px;
    }
  }

  .order{
    padding: 48px 20px;
    &__desc{
      margin-top: 12px;
    }
    &__tabs{
      margin-top: 32px;
    }
    &__content{
      margin-top: 20px;
    }
    &__form{
      grid-template-columns: 100%;
    }
  }

  .cart-empty{
    padding: 50px 0;
    &__img{
      width: 140px;
    }
    &__title{
      margin-top: 30px;
    }
    &__link{
      margin-top: 25px;
      width: 100%;
    }
  }
}
