@import "../mixins";

.menu{
  position: absolute;
  height: 110vh;
  max-height: calc(100vh - 210px);
  width: 100%;
  top: 150px;
  left: 0;
  background: $lightBlue;
  box-shadow: 0px 15px 30px rgba(0, 33, 51, 0.1);
  padding: 40px 0 60px;
  @media screen and (min-width: 1201px){
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  .container{
    height: 100%;
  }
  &__scroll{
    height: 100%;
    overflow-y: auto;
    @include scroll;
  }
  &__tabs{
    display: flex;
    &-wrap{
      display: flex;
      align-items: center;
      padding: 8px;
      background: $lightBlue2;
      border-radius: 50px;
    }
  }
  &__tab{
    display: flex;
    align-items: center;
    border-radius: 60px;
    padding: 0 24px;
    height: 41px;
    font-size: 14px;
    cursor: pointer;
    transition: all .3s;
    &:not(:last-child){
      margin-right: 12px;
    }
    @media screen and (min-width: 1201px){
      &:hover,
      &._active{
        background-color: $lightBlue;
      }
      &._active{
        pointer-events: none;
      }
    }
  }
  &__cell1{
    margin-top: 28px;
  }
  &__content,
  &__subcontent{
    display: none;
    &._active{
      display: block;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: 345px calc(100% - 345px);
  }
  &__link{
    min-height: 54px;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 5px 60px 5px 20px;
    font-weight: 500;
    font-size: 16px;
    line-height: 135%;
    background-image: url(../img/right-icon.svg);
    background-position: right 20px center;
    background-repeat: no-repeat;
    @media screen and (min-width: 1201px){
      &._active,
      &:hover{
        background-color: #FFFFFF;
        color: $primary;
        background-image: url(../img/right-icon2.svg);
      }
      &._active{
        pointer-events: none;
      }
    }
  }
  &__cell2{
    padding: 0 25px;
  }
  &__grid2{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 35px;
  }
  &__links2{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__link2,
  &__sublink2{
    margin-right: auto;
    font-size: 16px;
    line-height: 135%;
    &:hover{
      color: $primaryHover;
    }
  }
  &__link2{
    font-weight: 500;
    margin-bottom: 8px;
    &-show{
      display: flex;
      align-items: center;
      margin: 32px auto 0 0;
      font-weight: 500;
      font-size: 16px;
      line-height: 135%;
      color: $primary;
      svg{
        margin-left: 8px;
      }
      &:hover{
        color: $primaryHover;
      }
    }
  }
  &__sublink2{
    margin-top: 12px;
    &._hidden{
      display: none;
    }
  }
  &__link3,
  &__nav{
    display: none;
  }
  &__cos{
    display: grid;
    grid-template-columns: repeat(6,1fr);
    gap: 20px;
  }
  &__co{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 12px;
    background: #FFFFFF;
    border-radius: 12px;
    &:hover{
      .menu__co-title{
        color: $primaryHover;
      }
    }
    &-img{
      height: 40px;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-title{
      font-size: 14px;
      line-height: 120%;
      margin-top: 4px;
      color: $dark;
    }
  }
}


@media screen and (max-width: 1500px){
  .menu{
    &__cos{
      grid-template-columns: repeat(5,1fr);
    }
  }
}


@media screen and (max-width: 1250px){
  .menu{
    &__grid2{
      grid-template-columns: 100%;
    }
    &__cos{
      grid-template-columns: repeat(4,1fr);
    }
  }
}


@media screen and (max-width: 1200px){
  .menu{
    height: unset;
    max-height: unset;
    position: static;
    background-color: transparent;
    padding: 40px 0 32px;
    box-shadow: none;
    order: 2;
    .container{
      padding: 0;
    }
    &__link3{
      display: block;
      padding-bottom: 16px;
      border-bottom: 1px solid $outlineColor;
      font-weight: 500;
    }
    &__tabs{
      margin-top: 12px;
      display: block;
      padding-bottom: 32px;
      border-bottom: 1px solid $outlineColor;
      &-wrap{
        display: block;
        background-color: transparent;
        padding: 0;
      }
    }
    &__tab{
      padding: 12px 30px 12px 0;
      height: unset;
      background-image: url(../img/right-icon.svg);
      background-position: right center;
      background-repeat: no-repeat;
      margin: 0 !important;
      font-size: 16px;
      font-weight: 500;
    }
    &__cell1,
    &__cell2{
      position: fixed;
      top: 128px;
      left: 0;
      margin: 0;
      background-color: $lightBlue;
      width: 100%;
      height: calc(100vh - 130px);
      z-index: 20;
      padding: 32px 36px 246px;
      display: none;
      overflow-y: auto;
      &._active{
        display: block;
      }
    }
    &__nav{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 16px;
      border-bottom: 1px solid $outlineColor;
      margin-bottom: 12px;
    }
    &__back,
    &__chapter{
      font-weight: 500;
      font-size: 16px;
      line-height: 135%;
    }
    &__back{
      display: flex;
      align-items: center;
      color: $dark;
      svg{
        margin-right: 8px;
      }
    }
    &__chapter{
      color: $primary;
      &:hover{
        color: $primaryHover;
      }
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__grid2{
      gap: 12px;
    }
    &__link{
      background-color: transparent !important;
      min-height: 46px;
      padding: 5px 0;
    }
    &__link2{
      margin-bottom: 0;
    }
    &__link2,
    &__sublink2{
      font-weight: 500;
      padding: 6px 0;
    }
  }

  .header-adv:not(._hidden){
    &~.header{
      .header__mob,
      .menu__cell1,
      .menu__cell2{
        top: 168px;
        height: calc(100vh - 168px);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .menu{
    &__cos{
      grid-template-columns: repeat(3,1fr);
    }
  }
}



@media screen and (max-width: 840px){
  .menu{
    &__cos{
      grid-template-columns: repeat(2,1fr);
      gap: 15px;
    }
    &__cell1,
    &__cell2{
      top: 90px;
      height: calc(100vh - 90px);
      padding: 16px 16px 320px;
    }
  }

  .header-adv:not(._hidden){
    &~.header{
      .header__mob,
      .menu__cell1,
      .menu__cell2{
        top: 127px;
        height: calc(100vh - 127px);
      }
    }
  }
}

@media screen and (max-width: 576px){
  .menu{
    &__cos{
      grid-template-columns: repeat(1,1fr);
    }
  }
}
