.sale-page{
  padding-bottom: 50px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 10px));
    gap: 20px;
    margin-top: 40px;
  }
}

.sale-page2{
  padding-bottom: 50px;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 365px) 345px;
    gap: 90px 20px;
  }
  &__left{
    max-width: 947px;
  }
  &__right{
    .sale-card{
      padding: 20px;
      &__link{
        display: none;
      }
      &__title{
        margin-bottom: 0;
      }
    }
  }
  .content-text{
    img{
      display: block;
      width: 100%;
      margin: 60px 0;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
}

.sale-products{
  margin-top: 100px;
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    margin-top: 40px;
  }
}


@media screen and (max-width: 1100px){
  .sale-page{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .sale-page2{
    &__grid{
      grid-template-columns: 100%;
    }
    &__items{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 20px;
    }
  }
}


@media screen and (max-width: 767px){
  .sale-page{
    padding-bottom: 45px;
    &__grid{
      margin-top: 28px;
    }
  }

  .sale-page2{
    .content-text{
      img{
        margin-top: 40px;
      }
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .sale-products{
    &__items{
      grid-template-columns: repeat(2,calc(50% - 6px));
      gap: 12px;
      margin-top: 32px;
    }
  }
}
