.modal-backdrop {
    &.show {
        opacity: 0.7;
    }
}

.modal{
    @media screen and (min-width: 768px) {
        text-align: center;
        &:before {
            display: inline-block;
            vertical-align: middle;
            content: " ";
            height: 100%;
        }
        &-dialog{
            display: inline-block;
            text-align: left;
            vertical-align: middle;
        }
    }
    &-dialog{
        padding: 100px 80px;
        width: 627px;
        max-width: calc(100% - 20px);
        background-color: $lightBlue;
        border-radius: 20px;
        pointer-events: unset;
        position: relative;
    }
    &__close{
        background: #FFFFFF;
        box-shadow: 0px 5px 5px -2px rgba(0, 130, 201, 0.2);
        width: 50px;
        height: 50px;
        border-radius: 50%;
        position: absolute;
        right: 0;
        top: 0;
        transform: translate(50%,-50%);
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__title{
        text-align: center;
    }
    &__desc{
        margin-top: 16px;
        text-align: center;
    }
    &__product{
        display: flex;
        align-items: center;
        margin-top: 40px;
        img{
            width: 102px;
            flex-shrink: 0;
            margin-right: 19px;
        }
    }
    &__form{
        margin-top: 40px;
        display: grid;
        grid-template-columns: 100%;
        gap: 8px;
        .m-check{
            margin-top: 12px;
        }
    }
    &__btn{
        margin-top: 12px;
        height: 63px;
        width: 100%;
        font-weight: 500;
        font-size: 18px;
    }
    &__img{
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 280px;
        height: 100%;
        object-fit: cover;
        border-radius: 20px 0px 0px 20px;
    }
    &__icon{
        display: block;
        margin: 0 auto 20px;
    }
}

.call-modal{
    .modal{
        @media screen and (min-width: 768px){
            &-dialog{
                width: 907px;
                padding: 100px 80px 100px 360px;
            }
            &__img{
                display: block;
            }
        }
    }
}

.thank-modal{
    .modal{
        @media screen and (min-width: 768px){
            &-dialog{
                width: 400px;
                padding: 32px 40px;
            }
            &__title{
                max-width: 200px;
                margin: 0 auto;
            }
            &__desc{
                margin-top: 8px;
            }
        }
    }
}

.cookie-modal{
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    max-width: calc(100% - 10px);
    width: 600px;
    border-radius: 12px;
    padding: 16px 28px;
    display: flex;
    align-items: center;
    background-color: $lightBlue;
    z-index: 100;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &._show{
        opacity: 1;
        pointer-events: unset;
    }
    &__nav{
        flex-shrink: 0;
        margin-left: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__content,
    &__confirm,
    &__link{
        font-size: 14px;
        line-height: 120%;
    }
    &__content{
        flex-grow: 1;
    }
    &__confirm{
        padding: 0 14px;
        height: 37px;
        font-weight: 500;
        color: $primary;
        margin-bottom: 4px;
    }
    &__link{
        color: $dark;
    }
}


@media screen and (max-width: 767px){
    .modal{
        &-dialog{
            padding: 80px 15px;
            margin: 0 auto;
            max-width: 100%;
            width: 100%;
            height: 100vh;
            overflow-y: auto;
            border-radius: 0;
        }
        &__close{
            top: 15px;
            right: 15px;
            transform: unset;
            width: 40px;
            height: 40px;
        }
        &__product{
            margin-top: 32px;
            font-size: 16px;
            img{
                width: 85px;
                margin-right: 12px;
            }
        }
        &__form{
            margin-top: 32px;
        }
        &__icon{
            width: 40px;
        }
    }

    .cookie-modal{
        max-width: 100%;
        bottom: 0;
        padding: 12px;
        display: block;
        border-radius: 0;
        &__nav{
            flex-direction: row;
            justify-content: space-between;
            margin: 20px 0 0;
        }
        &__confirm{
            margin: 0;
        }
    }
}


