.direction{
  padding: 50px 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 365px) 345px;
    justify-content: space-between;
  }
  &__sidebar{
    .catalog-nav{
      position: static;
      &__group{
        padding: 16px 20px;
      }
    }
  }
  &__pdf{
    display: flex;
    align-items: center;
    margin-top: 40px;
    img{
      flex-shrink: 0;
      margin-right: 12px;
      width: 62px;
      display: block;
    }
  }
}

.direction-manufacture{
  @media screen and (min-width: 768px){
    &__grid{
      display: flex;
      flex-wrap: wrap;
      margin-top: 16px;
    }
    .manufacture__item{
      width: 223px;
      min-height: 90px;
      margin: 20px 20px 0 0;
    }
  }
}

.direction-types{
  margin-top: 80px;
  &:first-child{
    margin-top: 0;
  }
}

.popular{
  padding: 50px 0;
  &__slider{
    margin-top: 40px;
    position: relative;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      display: none;
    }
  }
}

.manufacture--product{
  .manufacture__grid{
    margin-top: 36px;
  }
}

.products{
  padding: 50px 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 20px;
  }
  &__img{
    position: relative;
    padding-bottom: 64%;
    border-radius: 12px;
    overflow: hidden;
    margin-bottom: 16px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}


@media screen and (max-width: 1300px){
  .products{
    &__grid{
      grid-template-columns: repeat(3,1fr);
    }
  }
}


@media screen and (max-width: 1100px){
  .direction{
    &__inner{
      grid-template-columns: 100%;
    }
    &__sidebar{
      display: none;
    }
  }

  .products{
    &__grid{
      grid-template-columns: repeat(2,1fr);
    }
  }
}


@media screen and (max-width: 767px){
  .direction{
    padding-top: 0;
  }

  .direction-manufacture{
    &__grid{
      margin-top: 20px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 12px;
    }
  }

  .direction-types{
    margin-top: 60px;
  }

  .popular{
    &__slider{
      margin-top: 28px;
    }
    .swiper{
      &-button{
        display: none;
      }
      &-pagination{
        display: flex;
      }
    }
  }

  .manufacture--product{
    padding: 0 0 30px;
    .manufacture__grid{
      margin-top: 20px;
    }
  }

  .products{
    padding: 30px 0 40px;
    &__grid{
      grid-template-columns: repeat(1,1fr);
      gap: 28px;
    }
    &__title{
      font-size: 16px;
    }
  }
}


@media screen and (max-width: 576px){
  .products{
    &__img{
      padding-bottom: 37.5%;
      border-radius: 8px;
      margin-bottom: 12px;
    }
  }
}
