.header{
  padding: 15px 0;
  background-color: $lightBlue;
  position: relative;
  top: 0;
  width: 100%;
  z-index: 10;
  transition: all .3s;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__right{
    flex-grow: 1;
    display: flex;
  }
  &__logo{
    display: block;
    width: 200px;
    margin-right: 47px;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__mob{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-grow: 1;
    max-width: 777px;
  }
  &__btn{
    width: 146px;
    height: 58px;
    border-radius: 8px;
    &-icon{
      flex-shrink: 0;
      width: 18px;
      height: 18px;
      display: block;
      background-image: url(../img/burger.svg);
      background-size: 100% auto;
      background-repeat: no-repeat;
      background-position: center;
      margin-right: 8px;
      transition: all .3s;
    }
  }
  &__info{
    flex-grow: 1;
    flex-shrink: 0;
    margin-left: 20px;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__call{
    margin-left: 20px;
    img{
      display: none;
    }
  }
  &__bottom{
    margin-top: 34px;
    display: flex;
    align-items: center;
    .header__btn{
      display: none;
    }
  }
  &__cart,
  &__call2{
    width: 177px;
    height: 58px;
    flex-shrink: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 135%;
    border: 1px solid $outlineColor;
    border-radius: 8px;
    &:hover{
      border-color: $dark;
      color: $black;
    }
    img{
      width: 20px;
      flex-shrink: 0;
      margin-right: 8px;
      display: block;
    }
    &-num{
      border-radius: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: 12px;
      padding: 0 10px;
      height: 28px;
      min-width: 28px;
      background-color: $primary;
      color: #fff;
      font-weight: 500;
      font-size: 14px;
    }
  }
  &__call2{
    display: none;
  }
  &__contacts{
    margin-left: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &._fix{
    position: sticky;
    box-shadow: 0px 15px 30px rgba(0, 33, 51, 0.1);
  }
  &._open-menu{
    .header__btn-icon{
      background-image: url(../img/burger2.svg);
    }
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 34px;
  &__link{
    line-height: inherit;
    color: $dark;
    &:not(:last-child){
      margin-right: 20px;
    }
  }
}

.header-consult{
  display: none;
}

.header-adv{
  color: #fff;
  background-color: $red;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 5px 70px;
  &._hidden{
    display: none;
  }
  &__close{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 40px;
    background-color: transparent;
  }
  &__title{
    font-weight: 500;
    font-size: 14px;
    line-height: 120%;
  }
  &__btn{
    height: 29px;
    padding: 0 14px;
    color: $black;
    font-weight: 400;
    font-size: 12px;
    border-radius: 4px;
    margin-left: 24px;
  }
}

@media screen and (max-width: 1250px){
  .header{
    &__logo{
      width: 180px;
      margin-right: 20px;
    }
  }
}


@media screen and (max-width: 1200px){
  .header{
    padding: 10px 0;
    &__inner{
      align-items: flex-end;
      justify-content: space-between;
    }
    &__mob{
      flex-direction: column;
      align-items: unset;
      flex-wrap: nowrap;
      z-index: 20;
      position: fixed;
      top: 128px;
      left: 0;
      height: calc(100vh - 128px);
      width: 100%;
      background-color: $lightBlue;
      overflow-y: auto;
      //overflow-y: hidden;
      max-width: unset;
      padding: 20px 36px 246px;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      .header__btn{
        display: none;
      }
    }
    &__top{
      display: none;
    }
    &__bottom{
      justify-content: flex-end;
      padding-bottom: 10px;
      margin: 0;
      .header__btn{
        margin-left: 24px;
        width: 128px;
        display: flex;
      }
    }
    &__contacts{
      order: -1;
      margin: 0 40px 0 0;
    }
    &__cart,
    &__call2{
      width: 58px;
      height: 58px;
      position: relative;
      &-title{
        display: none;
      }
      img{
        margin: 0;
      }
      &-num{
        position: absolute;
        top: -14px;
        right: -8px;
      }
    }
    &__call2{
      display: flex;
      margin-left: 24px;
      align-items: center;
      justify-content: center;
    }
    &._open-burger{
      .header{
        &__mob{
          opacity: 1;
          pointer-events: unset;
        }
        &__btn-icon{
          background-image: url(../img/burger2.svg);
        }
      }
    }
  }

  .nav{
    order: 3;
    display: block;
    &__link{
      display: block;
      padding: 12px 30px 12px 0;
      height: unset;
      background-image: url(../img/right-icon.svg);
      background-position: right center;
      background-repeat: no-repeat;
      font-size: 16px;
      line-height: 135%;
      font-weight: 500;
      color: $black;
      margin: 0 !important;
    }
  }

  .header-consult{
    display: block;
    padding: 40px 36px;
    background: #FFFFFF;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 30;
    &__top{
      display: flex;
      justify-content: space-between;
    }
    &__contacts{
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__btn{
      width: 100%;
      height: 46px;
      margin-top: 32px;
    }
  }
}


@media screen and (max-width: 840px){
  .header{
    &__mob{
      height: calc(100vh - 87px);
      top: 87px;
      padding: 7px 15px 320px;
    }
    &__logo{
      width: 115px;
      margin: 0;
    }
    &__call2,
    &__cart{
      width: 42px;
      height: 42px;
      margin-left: 24px;
      img{
        width: 18px;
      }
    }
    &__cart{
      &-num{
        height: 19px;
        min-width: 19px;
        font-size: 12px;
        padding: 0 6px;
        top: -5px;
        right: -6px;
      }
    }
    &__btn{
      width: 42px !important;
      height: 42px;
      margin-left: 12px !important;
      &-icon{
        margin: 0;
      }
      &-text{
        display: none;
      }
    }
    &__contacts{
      display: none;
    }
  }

  .header-consult{
    padding: 60px 15px;
    &__top{
      flex-direction: column;
      align-items: center;
    }
    &__email{
      margin-top: 24px;
    }
  }

  .header-adv{
    padding: 7px 35px;
    &__title{
      font-size: 10px;
      line-height: 110%;
      letter-spacing: -0.02em;
    }
    &__btn{
      padding: 0 10px;
      height: 21px;
      font-size: 8px;
      margin-left: 12px;
    }
    &__close{
      right: 15px;
      svg{
        width: 16px;
        height: auto;
      }
    }
  }
}
