@import 'vars';
html {
    width: 100%;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding-top: 40px;
}

body {
    background: $lightBlue;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 1510px;
    margin: 0 auto;
    padding: 0 35px;
    @media screen and (max-width: 767px) {
        padding: 0 15px;
    }
}

a {
    &:hover {
        color: $primary;
    }
    &:active{
        color: $primaryHover;
    }
}


@media screen and (max-width: 767px){
    .content{
        padding-top: 20px;
    }
}
