@import "../mixins";
.subgroup{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 40px 20px;
  }
}


.filter{
  border-radius: 16px;
  padding: 28px;
  border: 1px solid $outlineColor;
  &__content{
    display: grid;
    grid-template-columns: 100%;
    gap: 28px;
  }
  &__items{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12px;
    max-width: 222px;
    margin-top: 15px;
  }
  &__item{
    label{
      font-size: 16px;
      color: $black;
    }
  }
  &__switch{
    &-wrap{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &__top,
  &__bottom,
  &__bg{
    display: none;
  }
}


.direction--full{
  .subgroup__grid{
    grid-template-columns: repeat(4,1fr);
  }
}

.selected-filter{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
  color: $dark;
  &__groups{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: -20px;
  }
  &__group{
    margin-top: 20px;
    display: flex;
    align-items: center;
    &:not(:last-child){
      margin-right: 28px;
    }
  }
  &__items{
    display: flex;
    align-items: center;
  }
  &__title{
    margin-right: 8px;
  }
  &__item{
    display: flex;
    align-items: center;
    &:not(:last-child){
      margin-right: 8px;
    }
    &:hover{
      color: $primaryHover;
      path{
        stroke: $primaryHover;
      }
    }
    span{
      margin-right: 4px;
    }
  }
  &__clear{
    padding: 0 12.5px;
    height: 37px;
    flex-shrink: 0;
    margin-left: 10px;
    font-size: 14px;
    svg{
      margin-left: 8px;
    }
    &:hover,
    &:active{
      path{
        stroke:#fff;
      }
    }
  }
}

.mob-filter{
  display: none;
  &__views{
    display: none;
  }
}


@media screen and (max-width: 1300px){
  .subgroup{
    &__grid{
      grid-template-columns: repeat(2,1fr);
    }
  }
  .direction--full{
    .subgroup__grid{
      grid-template-columns: repeat(3,1fr);
    }
  }
}


@media screen and (max-width: 1100px){
  .subgroup-page{
    .direction__sidebar{
      display: block;
    }
  }

  .direction--full{
    .subgroup__grid{
      grid-template-columns: repeat(2,1fr);
    }
  }

  .mob-filter{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    &__toggle{
      height: 46px;
      padding: 0 16px;
      position: relative;
      svg{
        margin-left: 16px;
      }
    }
    &__has{
      position: absolute;
      top: -3px;
      right: -3px;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background-color: $red;
    }
  }

  .filter{
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 320px;
    border-radius: 0;
    border: none;
    background-color: $lightBlue;
    z-index: 1000;
    padding: 0;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &._active{
      opacity: 1;
      pointer-events: unset;
      &~.filter__bg{
        display: block;
      }
    }
    &__top{
      background-color: #fff;
      height: 54px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 500;
      font-size: 16px;
      line-height: 135%;
      padding: 0 50px;
      position: relative;
    }
    &__close{
      position: absolute;
      left: 28px;
      top: 50%;
      transform: translateY(-50%);
    }
    &__scroll{
      overflow-y: auto;
      height: calc(100vh - 54px);
    }
    &__content{
      padding: 48px 28px 208px;
    }
    &__bottom{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      padding: 28px;
      background-color: $lightBlue;
      @include grid100(12px);
      border-top: $border;
    }
    &__btn{
      height: 37px;
      width: 100%;
      font-size: 14px;
      svg{
        margin-left: 8px;
      }
    }
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background: #000000;
      opacity: 0.4;
      z-index: 900;
    }
  }
}


@media screen and (max-width: 576px){
  .filter{
    width: 100%;
  }
}


@media screen and (max-width: 767px){
  .selected-filter{
    display: none;
  }

  .mob-filter{
    margin-bottom: 40px;
    &__view{
      margin-left: 8px;
      &._active{
        rect,
        path{
          stroke: $primary;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .subgroup{
    &__grid{
      grid-template-columns: 100%;
      gap: 20px 12px;
      &._column{
        grid-template-columns: repeat(2,calc(50% - 6px));

        .sale-card2{
          padding: 12px;
          &__img{
            padding-bottom: 76%;
          }
          &__badges{
            flex-direction: column;
          }
          &__badge{
            height: 15px;
            padding: 0 3px;
            font-size: 6px;
            border-radius: 2px;
            margin: 0 auto 2px 0;
          }
          &__btn,
          &__btn2{
            font-size: 12px;
            height: 37px;
            ._hide{
              display: none;
            }
          }
          &__btn2._active{
            display: none;
          }
          &__cart{
            grid-template-columns: 100%;
            .sale-card2__btn2{
              display: none;
            }
            .cartcalc{
              height: 37px;
            }
          }
        }
      }
    }
  }

  .direction--full{
    .subgroup__grid{
      grid-template-columns: 100%;
    }
  }

  .mob-filter{
    &__views{
      display: flex;
      align-items: center;
    }
  }
}
