@import "../vars.scss";
.breadcrumbs {
    margin-bottom: 40px;
    &__item {
        font-size: 12px;
        line-height: 110%;
        position: relative;
        font-weight: 400;
        display: inline-flex;
        align-items: center;
        margin-right: 12px;
        &:not(:first-child) {
            &:before {
                content: '';
                display: inline-block;
                z-index: 1;
                width: 6px;
                height: 11px;
                background-image: url(../img/bread-arrow.svg);
                background-position: center;
                background-repeat: no-repeat;
                margin-right: 12px;
            }
        }
        &:last-child{
            //overflow: hidden;
            //white-space: nowrap;
            //text-overflow: ellipsis;
            //max-width: 260px;
        }
    }
    &__back{
        display: none;
        align-items: center;
        font-size: 12px;
        line-height: 110%;
        svg{
            margin-right: 4px;
        }
        &:hover{
            path{
                stroke: $primary;
            }
        }
        &:active{
            path{
                stroke: $primaryHover;
            }
        }
    }
}

.page-top{
    padding-bottom: 50px;
    &__wrap{
        padding: 48px;
        background-image: url(../img/top-bg.svg), radial-gradient(50% 50% at 77.23% 50%, #006399 0%, #0083C9 100%);
        background-repeat: no-repeat;
        background-position: right top;
        border-radius: 16px;
        color: #fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__content{
        max-width: 1100px;
    }
    &__desc{
        margin-top: 16px;
    }
    &__links{
        display: flex;
        align-items: center;
    }
    &__link{
        display: flex;
        align-items: center;
        &:not(:last-child){
            margin-right: 28px;
        }
        svg{
            margin-right: 12px;
        }
        &:hover{
            color: inherit;
            opacity: .8;
        }
    }
}


@media screen and (max-width: 1200px){
    .page-top{
        &__links{
            flex-direction: column;
            align-items: flex-end;
        }
        &__link{
            &:not(:last-child){
                margin: 0 0 20px;
            }
        }
    }
}


@media screen and (max-width: 767px){
    .breadcrumbs{
        &._small{
            .breadcrumbs{
                &__back{
                    display: flex;
                }
                &__wrap{
                    display: none;
                }
            }
        }
    }

    .page-top{
        padding-bottom: 40px;
        &__wrap{
            padding: 40px 28px;
            background-image: url(../img/top-bg2.svg), radial-gradient(50% 50% at 77.23% 50%, #006399 0%, #0083C9 100%);
        }
        &__desc{
            margin-top: 12px;
            font-size: 14px;
        }
        &__links{
            flex-direction: row;
            align-items: center;
        }
        &__link{
            &:not(:last-child){
                margin: 0 24px 0 0;
            }
            span{
                display: none;
            }
            svg{
                margin: 0;
            }
        }
    }
}
