.medium-text{
  font-weight: 500;
}

.gray-text{
  color: $dark;
}

.accent-text{
  color: $primary;
}

.display1{
  font-size: 46px;
  line-height: 120%;
  font-weight: 500;
}

.display2,
.content-text h2{
  font-weight: 500;
  font-size: 40px;
  line-height: 110%;
}

.display3,
.content-text h3{
  font-weight: 500;
  font-size: 26px;
  line-height: 120%;
}

.desc-text{
  font-size: 22px;
  line-height: 130%;
}

.body-text{
  font-size: 16px;
  line-height: 135%;
}

.body-text-big{
  font-size: 18px;
  line-height: 150%;
}

.body-text-small{
  font-size: 14px;
  line-height: 120%;
}

.body-text-big,
.body-text,
.body-text-small{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }
  b{
    font-weight: 700;
  }
}

.marker-text{
  display: flex;
  align-items: center;
  line-height: inherit;
  &:before{
    content: '';
    width: 6px;
    height: 6px;
    border-radius: 50%;
    flex-shrink: 0;
    background-color: $primary;
    margin-right: 8px;
  }
}


@media screen and (max-width: 767px){
  .display1,
  .display2,
  .content-text h2{
    font-size: 28px;
    line-height: 110%;
  }

  .display3,
  .content-text h3{
    font-size: 20px;
  }

  .desc-text{
    font-size: 15px;
    line-height: 135%;
  }

  .body-text-big,
  .body-text{
    font-size: 15px;
  }
}
