.main{
  overflow: hidden;
  padding-bottom: 50px;
  &__slider{
    position: relative;
  }
  .swiper{
    &-container{
      position: relative;
    }
    &-wrapper{
      align-self: stretch;
    }
    &-slide{
      height: auto;
      opacity: 0.3;
      &-active{
        opacity: 1;
      }
    }

  }
  &__slide{
    border-radius: 20px;
    overflow: hidden;
  }
  &__slide1{
    background-image: url(../img/main-bg.svg), radial-gradient(50% 50% at 77.23% 50%, #006399 0%, #0083C9 100%);
    background-position: 0 0;
    background-size: auto 100%, cover;
    background-repeat: no-repeat;
    color: #fff;
    display: flex;
    justify-content: space-between;
  }
  &__slide2{
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    position: relative;
    .main__title{
      margin: 0 0 20px;
    }
  }
  &__slide3{
    background: #19AFFF;
    color: #fff;
    display: flex;
    justify-content: space-between;
    .main__img{
      width: 684px;
      margin: 20px 30px 20px 0;
    }
  }
  &__content{
    padding: 80px 0 80px 60px;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 3;
  }
  &__title{
    max-width: 770px;
    margin-top: 20px;
  }
  &__desc{
    margin-top: 20px;
  }
  &__img{
    width: 588px;
  }
  &__link{
    margin-top: auto;
    width: 146px;
    height: 46px;
    background-color: $primaryHover;
    color: #fff;
    svg{
      margin-left: 16px;
      stroke:#fff;
    }
  }
  &__bg{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 2;
  }
  &__product{
    width: 597px;
    position: relative;
    &-img{
      width: 100%;
    }
    &-logo{
      position: absolute;
      bottom: 24px;
      left: 50%;
      transform: translateX(-50%);
      max-width: 100%;
      background: rgba(255, 255, 255, 0.03);
      backdrop-filter: blur(12.5px);
      border-radius: 12px;
      z-index: 2;
      img{
        max-width: 100%;
      }
    }
  }
}

.catalog{
  padding: 50px 0;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__link{
    height: 63px;
    padding: 0 48px;
    font-size: 18px;
    svg{
      width: 18px;
      height: auto;
      margin-left: 20px;
    }
    &-icon{
      &-mob{
        display: none;
      }
    }
  }
  &__desc{
    margin-top: 16px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 20px;
    margin-top: 40px;
  }
}

.manufacture{
  padding: 50px 0;
  &__grid{
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(6,1fr);
    gap: 20px;
  }
  &__item{
    background: #FFFFFF;
    border-radius: 8px;
    padding: 10px 16px;
    min-height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      max-width: 100%;
      max-height: 100%;
      transition: all .3s;
    }
    &:hover{
      img{
        opacity: 0.6;
      }
    }
  }
}

.sale{
  overflow: hidden;
  &__slider{
    margin-top: 40px;
  }
  .swiper{
    @media screen and (min-width: 768px){
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(4,calc(25% - 15px));
        gap: 20px;
        transform: unset !important;
      }
      &-pagination{
        display: none;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
  .sale-card{
    grid-column: 1/3;
  }
}

.consult{
  max-width: calc(100% - 80px);
  background-image: url(../img/consult-bg.svg), radial-gradient(50% 50% at 77.23% 50%, #006399 0%, #0083C9 100%);
  background-size: auto 100%, cover;
  background-position: left bottom,0 0;
  background-repeat: no-repeat;
  border-radius: 20px;
  width: 100%;
  margin: 50px auto;
  padding: 60px 0;
  &__inner{
    display: grid;
    grid-template-columns: auto 467px;
    gap: 60px 30px;
  }
  &__content{
    color: #FFFFFF;
    max-width: 832px;
    padding: 40px 0;
  }
  &__desc{
    margin-top: 28px;
  }
  &__form{
    padding: 48px 40px 23px;
    background-color: #fff;
    border-radius: 16px;
    .fg{
      margin-bottom: 8px;
    }
    .m-check{
      margin-top: 20px;
    }
  }
  &__btn{
    margin-top: 20px;
    width: 100%;
    height: 63px;
    font-size: 18px;
  }
}

.info{
  padding: 50px 0;
  overflow: hidden;
  &__text{
    max-width: 900px;
  }
  &__desc{
    margin-top: 40px;
  }
  &__slider{
    margin-top: 80px;
    @media screen and (min-width: 768px){
      .swiper{
        &-wrapper{
          transform: unset !important;
          width: calc(100% + 4px);
          display: grid;
          grid-template-columns: repeat(3,calc(100% / 3));
        }
        &-slide{
          width: 100% !important;
          margin-top: -1px;
          transform: translateX(-1px);
          &:nth-child(1),
          &:nth-child(2),
          &:nth-child(3){
            margin-top: 0;
          }
          &:nth-child(3n-2){
            transform: translateX(0);
          }
          &:nth-child(3n){
            transform: translateX(-3px);
          }
        }
        &-pagination{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .manufacture{
    &__grid{
      grid-template-columns: repeat(5,1fr);
    }
  }
}


@media screen and (max-width: 1300px){
  .main{
    &__content{
      padding: 60px 0 60px 40px;
    }
  }

  .catalog{
    &__grid{
      grid-template-columns: repeat(3,1fr);
    }
  }

  .manufacture{
    &__grid{
      grid-template-columns: repeat(4,1fr);
    }
  }

  .sale{
    .swiper{
      @media screen and (min-width: 768px){
        &-wrapper{
          grid-template-columns: repeat(2,calc(50% - 10px));
        }
      }
    }
  }

  .consult{
    max-width: calc(100% - 24px);
    padding: 80px 30px;
    background-image: url(../img/consult-bg2.svg),radial-gradient(50% 50% at 77.23% 50%,#006399 0,#0083c9 100%);
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      padding: 0;
    }
  }
}


@media screen and (max-width: 1200px){
  .main{
    .swiper{
      &-button{
        display: none;
      }
    }
    &__content{
      padding: 60px 40px;
    }
    &__slide1{
      background-size: cover;
      background-position: center;
    }
    &__img,
    &__product{
      display: none;
    }
    &__link{
      margin-top: 89px;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__inner{
      padding-bottom: 123px;
      position: relative;
    }
    &__top{
      display: block;
    }
    &__link{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }
    &__grid{
      grid-template-columns: repeat(2,1fr);
    }
  }

  .manufacture{
    &__grid{
      grid-template-columns: repeat(3,1fr);
    }
  }
}


@media screen and (max-width: 767px){
  .main{
    .swiper{
      &-pagination{
        margin-top: 16px;
      }
    }
    &__slide1{
      background-image: url(../img/main-bg-mob.svg), radial-gradient(50% 50% at 77.23% 50%, #006399 0%, #0083C9 100%);
      background-position: center bottom;
    }
    &__content{
      padding: 40px 28px;
    }
    &__title{
      font-size: 22px;
    }
    &__link{
      margin-top: 75px;
      width: 137px;
      height: 41px;
      font-size: 14px;
      svg{
        width: 14px;
        height: auto;
      }
    }
  }

  .catalog{
    &__inner{
      padding-bottom: 58px;
    }
    &__grid{
      margin-top: 32px;
      gap: 12px;
      grid-template-columns: 100%;
    }
    &__link{
      height: unset;
      font-size: 14px;
      line-height: 120%;
      background-color: transparent !important;
      color: $primary !important;
      svg{
        stroke: $primary !important;
        margin-left: 8px;
      }
      &-icon{
        &-desktop{
          display: none;
        }
        &-mob{
          display: unset;
        }
      }
    }
  }

  .manufacture{
    &__grid{
      gap: 12px;
      margin-top: 32px;
    }
    &__item{
      padding: 6px 12px;
      min-height: 58px;
    }
  }

  .sale{
    &__slider{
      margin-top: 32px;
    }
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
      }
    }
    .sale-card{
      grid-column: unset;
    }
  }

  .consult{
    border-radius: 0;
    max-width: 100%;
    padding: 50px 0 60px;
    background-image: url(../img/consult-bg3.svg),radial-gradient(50% 50% at 77.23% 50%,#006399 0,#0083c9 100%);
    &__inner{
      gap: 40px;
    }
    &__desc{
      margin-top: 16px;
    }
    &__form{
      padding: 32px 20px;
    }
    &__btn{
      height: 53px;
      font-size: 14px;
    }
  }

  .info{
    &__desc{
      margin-top: 28px;
    }
    &__slider{
      margin-top: 60px;
      .swiper{
        &-wrapper{
          align-items: stretch;
        }
        &-slide{
          height: auto;
        }
      }
    }
  }
}


@media screen and (max-width: 610px){
  .manufacture{
    &__grid{
      grid-template-columns: repeat(2,1fr);
    }
  }
}
