.contacts{
  padding: 50px 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 40px 20px;
  }
  &__group{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 50px;
    flex-shrink: 0;
    margin-right: 24px;
    img{
      width: 100%;
    }
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
  }
  &__item{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    a{
      &:not(:last-child){
        margin-right: 9px;
      }
    }
  }
}

.map{
  height: 360px;
  margin-top: 100px;
  border-radius: 16px;
  overflow: hidden;
}

.requisite{
  margin-top: 100px;
  max-width: 900px;
  &__text{
    margin-top: 24px;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__item{
      align-items: unset;
      flex-direction: column;
    }
  }
  @media screen and (min-width: 768px){
    .contacts{
      &__group{
        display: block;
      }
      &__icon{
        display: block;
        margin: 0 0 24px;
      }
    }
  }
}


@media screen and (max-width: 767px){
  .contacts{
    padding-top: 40px;
    &__grid{
      grid-template-columns: 100%;
    }
    &__group{
      .body-text-big{
        font-size: 18px;
      }
      .body-text{
        font-size: 16px;
      }
    }
    &__icon{
      width: 40px;
      margin-right: 32px;
    }
  }

  .map{
    height: 300px;
    margin-top: 90px;
  }

  .requisite{
    margin-top: 90px;
    &__text{
      margin-top: 20px;
    }
  }
}
