.cartcalc{
  border: 1px solid $outlineColor;
  border-radius: 8px;
  height: 46px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 14px;
  button{
    flex-shrink: 0;
  }
  svg{
    stroke: $primary;
  }
  input{
    flex-shrink: 0;
    width: calc(100% - 52px);
    text-align: center;
    border: none;
    font-weight: 500;
    font-size: 18px;
    color: $primary;
  }
}

.fg {
  position: relative;
  margin: 0;
  input {
    border: 1px solid $outlineColor;
    border-radius: 8px;
    padding: 0 20px;
    height: 62px;
    width: 100%;
    font-size: 16px;
    line-height: 135%;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: $dark;
    }
    &:hover,
    &:focus{
      border-color: $dark;
    }
    &:disabled{
      background: #F0F0F0;
      border-color: $outlineColor;
      pointer-events: none;
      &+label{
        color: $outlineColor;
      }
    }
    &:focus,
    &.input-active {
      &+label {
        font-size: 12px;
        line-height: 110%;
        top: 13px;
      }
    }
  }
  label {
    transition: all .1s;
    pointer-events: none;
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 16px;
    line-height: 135%;
    color: $dark;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-image: url(../img/check.svg);
        background-color: $primary;
        border-color: $primary;
        background-repeat: no-repeat;
        background-position: center;
      }
    }
    &:hover{
      &+label:before{
        border-color: $primary;
      }
    }
    &:hover:checked{
      &+label:before{
        border-color: $primaryHover;
        background-color: $primaryHover;
      }
    }
    &:active{
      &+label:before{
        border-color: $primary;
      }
    }
  }
  label {
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 120%;
    cursor: pointer;
    display: flex;
    align-items: center;
    color: $dark;
    &:before {
      content: '';
      border-radius: 4px;
      border: 1px solid $outlineColor;
      width: 20px;
      height: 20px;
      flex-shrink: 0;
      margin-right: 12px;
    }
    a{
      position: relative;
      z-index: 2;
      text-decoration: underline;
    }
  }
}

.m-switch{
  position: relative;
  input{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
    &:checked+label{
      background-color: $primary;
      &:before{
        left: 19px;
      }
    }
  }
  label{
    width: 38px;
    height: 22px;
    background: $outlineColor;
    border-radius: 100px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background-color: #fff;
      top: 50%;
      transform: translateY(-50%);
      left: 3px;
      transition: all .2s;
    }
  }
}


@media screen and (max-width: 767px){
  .fg{
    input{
      height: 57px;
      font-size: 14px;
      line-height: 120%;
      &:focus,
      &.input-active {
        &+label {
          font-size: 9px;
        }
      }
    }
    label{
      font-size: 14px;
      line-height: 120%;
    }
  }

  .m-check{
    label{
      font-size: 12px;
      line-height: 110%;
    }
  }
}
